import Vue from 'vue'
import App from './App.vue'
import moment from 'moment'
Vue.prototype.moment = moment;
const isProd = process.env.NODE_ENV === 'production';
import axios from 'axios'
import VueAxios from 'vue-axios'
import VueGtm from 'vue-gtm';
import VueRouter from 'vue-router'
import VueMeta from 'vue-meta'

//import pages
import HomePage from './pages/Home';
import LifestylePage from './pages/Lifestyle';
import MainScreen from './pages/MainScreen';
import UnderwaterPage from './pages/UnderwaterPage';
import WorkshopPage from './pages/WorkshopPage';
import TeacherTrainingCentrePage from './pages/TeacherTrainingCentrePage';
import TeensAndKidsPage from './pages/TeensAndKidsPage';

import EventBus from './event-bus';
import formProgram from './components/forms/formProgram';
import formFreePromo from './components/forms/formFreePromo';
import sectionReviews from './components/sections/reviews';
import sectionVideo from './components/sections/video';
import footer from './components/footer';
import wwSessionTracker from './ww-tracker';
import VueSpinners from 'vue-spinners';
const VueScrollTo = require('vue-scrollto');

/** import FA components */
import {library} from '@fortawesome/fontawesome-svg-core';
import {FontAwesomeIcon} from '@fortawesome/vue-fontawesome'
import {faEnvelope, faUser, faAddressBook, faPhone} from '@fortawesome/free-solid-svg-icons';
import {faFacebook, faTelegram, faYoutube, faInstagram} from '@fortawesome/free-brands-svg-icons';

/**
 * Page Components
 */
window.google = window.google ? window.google : {};

library.add(faEnvelope);
library.add(faUser);
library.add(faFacebook);
library.add(faTelegram);
library.add(faAddressBook);
library.add(faPhone);
library.add(faYoutube);
library.add(faInstagram);
/** Enable component */
Vue.component('font-awesome-icon', FontAwesomeIcon);
Vue.component('form-program', formProgram);
Vue.component('form-free-promo', formFreePromo);
Vue.component('section-video', sectionVideo);
Vue.component('section-reviews', sectionReviews);
Vue.component('section-footer', footer);
Vue.prototype.$wwSessionTracker = wwSessionTracker;

Vue.config.productionTip = !isProd;
Vue.use(VueAxios, axios);

Vue.use(VueSpinners);
Vue.use(VueRouter);
Vue.use(VueMeta, {
  refreshOnceOnNavigation: true
});

Vue.use(VueScrollTo);

Vue.use(VueScrollTo, {
  container: "body",
  duration: 300,
  easing: "ease",
  offset: 0,
  force: true,
  cancelable: true,
  onStart: false,
  onDone: false,
  onCancel: false,
  x: false,
  y: true
});

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/lifestyle',
    name: 'lifestyle',
    component: LifestylePage
  },
  {
    path: '/main-screen',
    name: 'main_screen',
    component: MainScreen
  },
  {
    path: '/underwater/group/:id',
    name: 'underwater',
    component: UnderwaterPage
  },
  {
    path: '/workshop',
    name: 'workshop',
    component: WorkshopPage
  },
  {
    path: '/teacher-training',
    name: 'teacher_training',
    component: TeacherTrainingCentrePage
  },
  {
    path: '/teens-kids',
    name: 'teens_and_kids',
    component: TeensAndKidsPage
  },
];

const router = new VueRouter({
  routes,
  mode: 'history',
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});


Vue.mixin({
  methods: {
    openModal() {
      EventBus.$emit('eventOpenModal');
    },
    openVideoModal() {
      EventBus.$emit('eventOpenVideoModal');
    },
    location(href, newWindow = false) {
      if (newWindow) {
        window.open(href);
      } else {
        document.location = href;
      }
    }
  }
});

Vue.use(VueGtm, {
  id: 'GTM-TR9RSGP',
  enabled: isProd,
  debug: !isProd,
  loadScript: false,
  vueRouter: router,
});


// initialize Vue application
new Vue({
  el: '#app',
  router,
  data() {
    return {}
  },
  methods: {},
  beforeCreate: function () {
    this.$wwSessionTracker.track();
  },
  render: h => h(App),
  mounted() {
    document.dispatchEvent(new Event("x-app-rendered"));
    // You'll need this for renderAfterDocumentEvent.
    document.dispatchEvent(new Event('custom-render-event'));

    Array.prototype.max = function () {
      return Math.max.apply(null, this);
    };
    let panels = document.querySelectorAll(".simple-block");
    let heights = Object.values(Array.prototype.slice.call(panels).map(function (panel) {
      return panel.offsetHeight;
    }));

    for (let i = 0; i < panels.length; i++) {
      document.getElementsByClassName("simple-block")[i].style.height = heights.max() + 'px';
    }
  }
});
window.snapSaveState = () => {
  document.querySelector('#app').setAttribute('data-server-rendered', 'true')
};
