<template>
    <div class="form-prices">
        <div class="h2">Найближчі старти <br>LIFESTYLE груп</div>
        <div class="form-prices-body">
            <div
                    v-for="item in groups"
                    class="form-prices-item">
                <div class="form-prices-item--head">
                    {{ item.level_title }}
                </div>
                <div class="form-prices-item--body">
                    <div class="form-prices-item--time">
                        <div class="--title">Стартуємо: <span>{{ moment(new Date(item.start_date)).format('D.M.Y')}}</span></div>
                        <div class="--body">{{ moment(item.time, 'h:mm:ss').format('HH:mm') }}</div>
                        <div class="--subtitle">
                            <template v-for="(day, key) in JSON.parse(item.days)">
                                {{ daysList[day].title_ua }}<template v-if="key < JSON.parse(item.days).length-1">, </template>
                            </template>
                        </div>
                    </div>
                    <ul>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>Розмовний клуб з носіями мови</p>
                        </li>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>WinTalk та Winletter включені в курс</p>
                        </li>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>#OnlyEnglish - тільки англійська</p>
                        </li>
                        <li>
                            <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                            <p>Тривалість семестру: 3 місяці</p>
                        </li>
                    </ul>
                </div>
                <div class="form-prices-item--footer">
                    <div class="form-prices-item--footer--before">
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                        <i></i>
                    </div>
                    <div class="--title">Семестр занять за програмою:</div>
                    <div class="--body">
                        <div class="price" :class="{'old-price' : item.discount_price > 0}">
                            {{ item.price }}<span>грн.</span>
                            <i v-if="item.discount_price > 0"><img src="../../assets/images/layers.svg#layer-23" alt="#"></i>
                        </div>
                        <div v-if="item.discount_price > 0" class="discount-price">{{ item.discount_price }}</div>
                    </div>


                    <div class="--buttons">
                        <div class="btn-box">
                            <div class="btn btn-1" @click="openModal(item)">
                                Дізнайся свій рівень
                            </div>
                        </div>
                        <a href="#">Повний розклад цього курсу</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import EventBus from './../../event-bus';
    export default {
        components: {},
        props: ['propItems'],
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "full_title": '',
                    "group" : null
                },
                groups: null,
                daysList: null
            };
        },
        methods: {
            getJson(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.open('GET', url, true);
                xhr.responseType = 'json';
                xhr.onload = function() {
                    var status = xhr.status;
                    if (status === 200) {
                        callback(null, xhr.response);
                    } else {
                        callback(status, xhr.response);
                    }
                };
                xhr.send();
            },
            openModal(item) {
                this.data.data = item;
                this.data.data.full_title = item.level_title;
                EventBus.$emit('eventOpenModal', this.data);
            },
            filterData(data) {
                return data.filter(obj => {
                    return ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                });
            }
        },
        created() {
            let _this = this;
            _this.daysList = (require('./../../jsons/daysList.json'));
            this.getJson('https://management.winwin.education/api/group/search?course=lifestyle&type=adults', function(err, data){ _this.groups = _this.filterData(data.data).slice(0, _this.propItems); });

        },
        mounted() {
        }
    }
</script>
