<script>
    import EventBus from './../event-bus';
    import tmpForm from './form';
    export default {
        components: {tmpForm},
        data () {
            return {
                showModal: false,
                data: null
            }
        },
        methods: {
            show(data = null) {
                this.data = data;
                console.log(data);
