<script>
    import EventBus from './../../event-bus';
    export default {
        components: {},
        // props: ['propItems'],
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "full_title": '',
                    "group" : null
                },
                groups: null,
                daysList: null
            };
        },
        methods: {
            getJson(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.open('GET', url, true);
                xhr.responseType = 'json';
                xhr.onload = function() {
                    var status = xhr.status;
                    if (status === 200) {
                        callback(null, xhr.response);
                    } else {
                        callback(status, xhr.response);
                    }
                };
                xhr.send();
            },
            openModal(item) {
                this.data.data = item;
                this.data.data.full_title = item.level_title;
                EventBus.$emit('eventOpenModal', this.data);
            },
            filterData(data) {
                return data.filter(obj => {
                    return ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                });
            }
        },
        created() {
            let _this = this;
            _this.daysList = (require('./../../jsons/daysList.json'));
            this.getJson('https://management.winwin.education/api/worshops/search?type=adults', function(err, data){ _this.groups = _this.filterData(data.data).slice(0, _this.propItems); console.log(data.data) });

        },
        mounted() {
        }
    }
</script>
