    <div class="form-closest-group">
        <div class="h2">Найближчі старти <br>LIFESTYLE груп</div>
        <div class="form-prices-body">
            <div v-if="group"
                    class="form-prices-item chb-container">
                <div class="row">
                    <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                        <div class="form-prices-item--head">
                            {{ group.level_title }}
                        </div>
                        <div class="form-prices-item--body">
                            <div class="form-prices-item--time">
                                <div class="--title">Стартуємо: <span>{{ moment(new Date(group.start_date)).format('D.M.Y') }}</span></div>
                                <div class="--body">{{ moment(group.time, 'h:mm:ss').format('HH:mm') }}</div>
                                <div class="--subtitle">
                                    <template v-for="(day, key) in JSON.parse(group.days)">
                                        {{ daysList[day].title_ua }}<template v-if="key < JSON.parse(group.days).length-1">, </template>
                                    </template>
                                </div>
                            </div>
                            <ul>
                                <li>
                                    <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                                    <p>Розмовний клуб з носіями мови</p>
                                </li>
                                <li>
                                    <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                                    <p>WinTalk та Winletter включені в курс</p>
                                </li>
                                <li>
                                    <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                                    <p>#OnlyEnglish - тільки англійська</p>
                                </li>
                                <li>
                                    <i><img src="../../assets/images/icons.svg#icon-14" alt="#"></i>
                                    <p>Тривалість семестру: 3 місяці</p>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                        <div class="form-prices-item--footer">
                            <div class="form-prices-item--footer--before-2">
                                <i></i>
                                <i></i>
                                <i></i>
                                <i></i>
                                <i></i>
                                <i></i>
                                <i></i>
                            </div>
                            <div class="--title">Семестр занять за програмою:</div>
                            <div class="--body">
                                <div class="price" :class="{'old-price' : group.discount_price > 0}">
                                    {{ group.price }}<span>грн.</span>
                                    <i v-if="group.discount_price > 0"><img src="../../assets/images/layers.svg#layer-23" alt="#"></i>
                                </div>
                                <div v-if="group.discount_price > 0" class="discount-price">{{ group.discount_price }}</div>
                            </div>
                            <div class="--buttons">
                                <div class="btn-box">
                                    <div class="btn btn-1" @click="openModal(group)">
                                        Дізнайся свій рівень
                                    </div>
