                </div>



            </div>
        </div>
    </div>
</template>

<script>
    import EventBus from './../../event-bus';
    export default {
        components: {},
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "full_title": '',
                    "group" : null,
                    data : {}
                },
                group: null,
                daysList: null
            };
        },
        methods: {
            getJson(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.open('GET', url, true);
                xhr.responseType = 'json';
                xhr.onload = function() {
                    var status = xhr.status;
                    if (status === 200) {
                        callback(null, xhr.response);
                    } else {
                        callback(status, xhr.response);
                    }
                };
                xhr.send();
            },
            openModal(item) {
                this.data.data = item;
                this.data.data.full_title = item.level_title;
                EventBus.$emit('eventOpenModal', this.data);
            },
        },
        created() {
            let _this = this,
                id = this.$route.params.id;
            _this.daysList = (require('./../../jsons/daysList.json'));
            this.getJson('https://management.winwin.education/api/group/search?id='+id, function(err, data){ _this.group = data.data[0]; });

        },
        mounted() {
        }
    }
</script>
