<template>
    <div >
        <hooper ref="form-workshop-schedule"
                :mouse-drag="true"
                :touch-drag="true"
                :short-drag="false"
                :wheel-control="false"
                @afterSlide="sliderUpdated"
                @updated="sliderUpdated" class="form-workshop-schedule">

        <slide class="form-workshop-schedule-item" v-for="n in 3">
            <div class="form-workshop-left-head">
                <div class="row">
                    <div class="col-lg-8 col-md-8 col-sm-7 col-xs-7">
                        <div class="workshop-schedule-item-title">Марафон WinGrammar</div>
                    </div>
                    <div class="col-lg-4 col-md-4 col-sm-5 col-xs-5 text-right">
                        <div class="workshop-time">
                            <div class="--date">25 Лютого</div>
                            <div class="--titme">19:00</div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="form-workshop-image" v-bind:style="{ backgroundImage: 'url(' + 'https://www.microsoft.com/ru-ru/microsoft-365/blog/wp-content/uploads/sites/54/2019/01/OfficeNews_365Mac-440x268.png' + ')' }"></div>

            <div class="h4">Що будемо робити?</div>
            <div class="workshop-list">
                <ul>
                    <li>
                        <i><img src="../../assets/images/icons.svg#ul-i" alt="#"></i>
                        <p>Твої знання стануть структурованими і в них не буде прогалин</p>
                    </li>
                    <li>
                        <i><img src="../../assets/images/icons.svg#ul-i" alt="#"></i>
                        <p>Граматика стане сильною стороною твоєї англійськоїн</p>
                    </li>
                    <li>
                        <i><img src="../../assets/images/icons.svg#ul-i" alt="#"></i>
                        <p>Ти перестанеш боятися говорити і будеш робити це без помилок</p>
                    </li>
                </ul>
            </div>
            <div class="workshop-icon-groups">
                <div class="workshop-icon-group">
                    <i><img src="../../assets/images/icons.svg#clock" alt="#"></i>
                    <p>90 хвилин</p>
                </div>
                <div class="workshop-icon-group">
                    <i><img src="../../assets/images/icons.svg#chat" alt="#"></i>
                    <p>Онлайн</p>
                </div>
            </div>

            <div class="form-workshop-left-foot">

                <div class="btn-box">
                    <div class="btn btn-1" @click="openModal()">
                        Записатися на цей воркшоп
                    </div>
                </div>
                <p><a href="#">Дізнатись більше</a></p>

            </div>

        </slide>
        </hooper>
    </div>

</template>

<script>
    import {
        Hooper,
        Slide,
        Pagination as HooperPagination
    } from 'hooper';
    import 'hooper/dist/hooper.css';
    import EventBus from './../../event-bus';
    export default {
        components: {
            Hooper,
            Slide,
            HooperPagination
        },
        // props: ['propItems'],
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "full_title": '',
                    "group" : null
                },
                groups: null,
                daysList: null
            };
        },
        methods: {
            getJson(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.open('GET', url, true);
                xhr.responseType = 'json';
                xhr.onload = function() {
                    var status = xhr.status;
                    if (status === 200) {
                        callback(null, xhr.response);
                    } else {
                        callback(status, xhr.response);
                    }
                };
                xhr.send();
            },
            openModal(item) {
                this.data.data = item;
                this.data.data.full_title = item.level_title;
                EventBus.$emit('eventOpenModal', this.data);
            },
            filterData(data) {
                return data.filter(obj => {
                    return ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                });
            },
            /**
             *
             */
            slidePrev() {
                this.$refs['form-workshop-schedule'].slidePrev();
            },

            /**
             *
             */
            slideNext() {
                this.$refs['form-workshop-schedule'].slideNext();
            },
            sliderUpdated() {
                try {
                    // document.getElementById('main-slider').getElementsByClassName("hooper")[0].style.height = document.getElementById('main-slider').getElementsByClassName('hooper-slide is-active is-current')[0].getElementsByClassName("slider-item")[0].offsetHeight+"px";
                } catch (e) {

                }
            }
        },
        created() {
            let _this = this;
            _this.daysList = (require('./../../jsons/daysList.json'));
            this.getJson('https://management.winwin.education/api/group/search?course=lifestyle&type=adults', function(err, data){ _this.groups = _this.filterData(data.data).slice(0, _this.propItems); });

        },
        mounted() {
        }
    }
</script>
