<template>

    <div class="form-base">
        <div class="form-base-header">
            {{ propTitle }}
        </div>
        <div class="form-base-body relative">
            <form action="">
                <div v-if="propData" class="form-block text-center form-text">
                    <div> Ви обрали іспит: </div>
                    <h2>{{ propData.data.full_title }}  </h2>
                    <div><span>{{ propData.data.exam_date }}</span>  </div>
                </div>
                <div class="form-block text-center form-text">
                    <div>Круто! Наші менеджери зв'яжуться з вами протягом декількох годин і розкажуть усі деталі.</div>
                </div>

                <div class="form-block">
                    <i class="far fa-user-circle"></i>
                    <input
                            required
                            v-model="data.name"
                            type="text"
                            name="name"
                            id="name"
                            class="input input-1"
                            placeholder="Як вас звати?"
                            :class="{'error' : validator.name === false}" @keypress="validate('name')">
                </div>

                <div class="form-block">
                    <i class="fas fa-mobile-alt"></i>
                    <input
                            required
                            v-model="data.phone"
                            v-mask="'+38 (0##) ###-##-##'"
                            type="tel"
                            name="phone"
                            id="phone"
                            class="input input-1"
                            placeholder="Ваш номер телефону"
                            :class="{'error' : validator.phone === false}" @keypress="validate('phone')">
                </div>

                <div class="form-block">
                    <i class="fas fa-at"></i>
                    <input v-model="data.email" type="email" name="email" id="email" class="input input-1" placeholder="Ваш Email">
                </div>

                <div class="text-center">
                    <div class="btn-box inline-block">
                        <button type="submit" class="button btn btn-1" :disabled="!btnEnable" @click="submit" style="outline: none;">Надіслати</button>
                    </div>
                </div>
            </form>
            <div class="form-success" v-if="response.success === true">
                <div class="form-success-title">Привіт!</div>
                <div class="form-success-descr paragraph">Ми тільки що отримали ваш запит. Ми зв'яжемося з Вами якомога швидше.</div>
                <a href="#" class="btn btn-1" @click="close()">Ну, ОК!</a>
            </div>
        </div>
    </div>

</template>

<script>
    import EventBus from './../event-bus';
    import {mask} from 'vue-the-mask'
    export default {
        directives: {mask},
        props: {
            propTitle: {
                type        : String,
                required    : true
            },
            propData: {
                required    : false
            }
        },
        data () {
            return {
                btnEnable : true,
                response    : { error: null, success: null },
                data        : { name: '', phone: '', email: '', source_id: 50, city_id: 2 },
                validator   : { name: null, phone: null }
            }
        },
        methods: {
            close() {
                this.response = { error: null, success: null };
                this.data = { name: '', phone: '', email: '', source_id: 50, city_id: 2 };
                EventBus.$emit('eventCloseModal')
            },
            submit(e) {
                const examData = this.propData ? this.propData.data : {};
                this.validator = { name: null, phone: null };
                if (this.data.name && this.data.phone && this.data.email && this.validator.name !== false && this.validator.phone !== false) {
                    this.btnEnable = false;
                    let _this = this;
                    const payload = {
                        lead      : _this.data,
                        tracker   : this.$wwSessionTracker.getTrackData()
                    };
                    payload.lead['exam_data'] = examData;
                    this.axios.post('https://management.winwin.education/api/lead/submit', payload)
                        .then(function (response) {
                            console.log(response);
                            _this.response.success = true;
                            _this.btnEnable = true;
                            _this.$gtm.trackEvent({
                                event: 'gaEvent',
                                eventCategory: 'Submit',
                                eventAction: 'Exam Form Submit',
                                eventLabel: window.location.pathname + window.location.hash,
                                eventValue: examData
                            });
                            let contentName = 'General Submission';
                            if (typeof examData.exam_date !== "undefined"){
                                contentName = examData.full_title;
                            }
                            fbq('track', 'Lead', { //eslint-disable
                                content_name: contentName,
                                content_category: window.location.pathname + window.location.hash,
                                value: 10,
                            });

                        })
                        .catch(function (error) {
                            console.log(error);
                            _this.btnEnable = true;
                        });
                    e.preventDefault();
                } else {
                    if (!this.data.phone || this.data.phone.length < 19) {
                        this.validator.phone = false;
                    }
                    if (!this.data.name || this.data.name.length < 2) {
                        this.validator.name = false;
                    }
                }
            },
            validate(type) {
                switch (type) {
                    case 'name':
                        if (this.data.name && this.data.name.length >= 2) {
                            this.validator.name = true;
                        }
                        break;
                    case 'phone':
                        if (this.data.phone && this.data.phone.length >= 19) {
                            this.validator.phone = true;
                        }
                        break;
                }
            }
        }
    }
</script>
