<template>
    <div class="form-schedule" id="form-schedule">
        <table class="hidden-xs">
            <thead>
                <tr>
                    <th>Рівень</th>
                    <th>Стартує</th>
                    <th>Час та дні</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
            <template v-for="item in groups">
                <tr>
                    <td><div><span><i>{{ item.level_code }}</i></span><p>{{ item.level_title }}</p></div></td>
                    <td><span>{{ new Date(item.start_date).toLocaleDateString()}}</span></td>
                    <td><p><span>{{ item.time }}</span>
                        <template v-for="(day, key) in JSON.parse(item.days)">
                            {{ daysList[day].short }}<template v-if="key < JSON.parse(item.days).length-1">, </template>
                        </template>
                    </p></td>
                    <td>
                        <div class="btn-box">
                            <div class="btn btn-1" @click="openModal()">
                                Дізнайся свій рівень
                            </div>
                        </div>
                    </td>
                </tr>
                <tr><td colspan="5" class="--white"></td></tr>
            </template>
            </tbody>
            <tfoot>
            </tfoot>
        </table>
        <div class="schedule-item-container">
            <div class="schedule-item">
                <div class="schedule-item-head"></div>
                <div class="schedule-item-body"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import EventBus from './../../event-bus';
    export default {
        components: {},
        data() {
            return {
                data: {
                    "selected": 'underwater',
                    "group" : null
                },
                groups: null,
                daysList: null
            };
        },
        methods: {
            changeCheckbox(index) {
                this.data.selected = index;

                var result = this.groups.filter(obj => {
                    return obj['course'] === index && ((new Date(obj['start_date']).getTime()) > new Date().getTime())
                })[0];

                this.data.start_date = result.start_date;
                this.data.price = result.price;
                this.data.discount_price = result.discount_price;
                this.data.id = result.id;

                this.data.group = result;

            },
            getJson(url, callback) {
                var xhr = new XMLHttpRequest();
                xhr.open('GET', url, true);
                xhr.responseType = 'json';
                xhr.onload = function() {
                    var status = xhr.status;
                    if (status === 200) {
                        callback(null, xhr.response);
                    } else {
                        callback(status, xhr.response);
                    }
                };
                xhr.send();
            },
            openModal() {
                EventBus.$emit('eventOpenModal', this.data);
            },
        },
        created() {
            let _this = this;
            _this.daysList = (require('./../../jsons/daysList.json'));
            console.log(this.daysList)
            this.getJson('https://management.winwin.education/api/group/search?course=lifestyle&type=adults', function(err, data){ _this.groups = data.data; });

        },
        mounted() {
        }
    }
</script>