<template>
    <div class="form-free-promo">
        <div class="row">
            <div class="col-lg-5 col-md-5 col-sm-6 col-xs-12 col-lg-offset-1 col-md-offset-1">
                <div class="h1">Безкоштовне пробне заняття</div>
                <p class="paragraph">Просто запишись та переконайся на власні очі та вуха</p>
                <div class="text-center">
                    <img src="../../assets/images/dynamic/images/image-1.png" alt="#">
                </div>
            </div>
            <div class="col-lg-4 col-md-4 col-sm-6 col-xs-12 col-lg-offset-1 col-md-offset-1">
                <form action="">
                    <div class="input-group">
                        <label for="name">Ім'я:<i>*</i></label>
                        <div class="input-form" :class="{'error' : validator.name === false}" >
                            <input type="text" name="name" v-model="data.name" id="name" required placeholder="Як до вас звертатися?">
                        </div>
                        <p class="error" v-if="validator.name === false">Поле обязательно для заполнения</p>
                    </div>
                    <div class="input-group">
                        <label for="phone">Номер телефона:<i>*</i></label>
                        <div class="input-form" :class="{'error' : validator.phone === false}">
                            <span>+380</span>
                            <input type="text" name="phone" v-model="data.phone" id="phone" required placeholder="00    00    00    000" v-mask="'(##) ###-##-##'">
                        </div>
                        <p class="error" v-if="validator.phone === false">Поле обязательно для заполнения</p>
                    </div>
                    <div class="input-group">
                        <label for="name">Email:</label>
                        <div class="input-form">
                            <input type="email" name="email" v-model="data.email" id="email" placeholder="Куди надсилати листи?">
                        </div>
                    </div>
                    <div class="input-group">
                    <div class="btn-box">
                        <button type="submit"  class="button btn btn-1" @click.prevent="submit">
                            Дізнайся свій рівень
                        </button>
                    </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import {mask} from 'vue-the-mask'
    export default {
        directives: {mask},
        components: {},
        data() {
            return {
                data: {name: "", phone: "", email: ""},
                validator   : { name: null, phone: null }
            };
        },
        methods: {
            submit(e) {
                const examData = this.propData ? this.propData.data : {};
                this.validator = { name: null, phone: null };
                if (this.data.name && this.data.phone && this.validator.name !== false && this.validator.phone !== false) {
                    this.btnEnable = false;
                    let _this = this;
                    const payload = {
                        lead      : _this.data,
                        tracker   : this.$wwSessionTracker.getTrackData()
                    };
                    payload.lead['exam_data'] = examData;
                    this.axios.post('https://management.winwin.education/api/lead/submit', payload)
                        .then(function (response) {
                            _this.response.success = true;
                            _this.btnEnable = true;
                            _this.$gtm.trackEvent({
                                event: 'gaEvent',
                                eventCategory: 'Submit',
                                eventAction: 'Exam Form Submit',
                                eventLabel: window.location.pathname + window.location.hash,
                                eventValue: examData
                            });
                            let contentName = 'General Submission';
                            if (typeof examData.exam_date !== "undefined"){
                                contentName = examData.full_title;
                            }
                            fbq('track', 'Lead', {
                                content_name: contentName,
                                content_category: window.location.pathname + window.location.hash,
                                value: 10,
                            });
                            console.log(123);

                        })
                        .catch(function (error) {
                            _this.btnEnable = true;
                        });
                    e.preventDefault();
                } else {
                    if (!this.data.phone || this.data.phone.length < 14) {
                        this.validator.phone = false;
                    }
                    if (!this.data.name || this.data.name.length < 2) {
                        this.validator.name = false;
                    }
                }
            },
            validate(type) {

                switch (type) {
                    case 'name':
                        if (this.data.name && this.data.name.length >= 2) {
                            this.validator.name = true;
                        }
                        break;
                    case 'phone':
                        if (this.data.phone && this.data.phone.length >= 19) {
                            this.validator.phone = true;
                        }
                        break;
                }
            }
        },
        created() {
        },
        mounted() {
        }
    }
</script>
